.user-register{
    position: relative;
    left: 20vw;
    top: 25vh;
    color:lightcoral;
    font-size: 1.3vw;
    width: 30vw;
}
.name-input{
    margin-bottom: 1vw;
    margin-left: 1.3vw;
}
.password-input{
    margin-bottom: 1vw;
    margin-left: 2.6vw;
}
.user-input{
    width: 20vw;
}
.user-title{
font-size: 2vw;
color:lightcoral;
text-align: center;
margin-bottom: 1vw;
padding-left: 3vw;
}
.user-btn{
    width: 6vw;
    height: 5vh;
    margin: 0 auto;
    margin-top: 1vw;
}
.user-btn button{   
    margin-left: 1vw;
    width: 100%;
  background-color: rgb(73, 105, 221);
  color:rgb(255, 255, 255);
}
@media screen and (max-width:1000px){
    /* .name-input{
        margin-bottom: 1vw;
        margin-left: 1.3vw;
    } */
    .user-title{
        font-size: 5vw;
        color:lightcoral;
        text-align: center;
        margin-bottom: 1vw;
        padding-left: 18vw;
        }
    .password-input{
        margin-bottom: 2vw;
        margin-left: 1.6vw;
    }
    .user-register{
        position: relative;
        left: 10vw;
        top: 15vh;
        color:lightcoral;
        font-size: 1.3vw;
        width: 30vw;
    }
    .user-input{
        width: 45vw 
    }
    .user-btn{
        width: 15vw;
        margin-left: 15vw;
    }
}